@import "/src/definitions";

.codeWarsWrapper {
  display: flex;
  flex-direction: column;

  .contentWrapper {
    min-height: 160px;
    display: flex;
    align-items: center;

    .firstColumn {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .secondColumn {
      margin-left: 60px;

      @media screen and (max-width: $width-adaptive-step-four){
        margin-left: 20px;
      }

      .rank {
        font-size: 100px;

        @media screen and (max-width: $width-adaptive-step-one){
          font-size: 55px;
        }
        @media screen and (max-width: $width-adaptive-step-three){
          font-size: 40px;
        }
        @media screen and (max-width: $width-adaptive-step-four){
          font-size: 35px;
        }
      }
    }

    .options {
      font-size: 20px;

      @media screen and (max-width: $width-adaptive-step-two){
        font-size: 14px;
      }
      @media screen and (max-width: $width-adaptive-step-three){
        font-size: 12px;
      }

      .title {
        font-weight: bold;
      }

      .value {
        color: $red-color
      }

      .numOfCodeWarsUsers {
        font-size: .75em;
        color: $font-darken-color;

        @media screen and (max-width: $width-adaptive-step-three){
          display: none;
        }
      }
    }
    }


  .buttonWrapper {
    margin-top: 15px;
    text-align: center;


    .refreshButton {
      display: inline-block;
      padding: 5px 20px;
      box-shadow: 0 0 2px 2px $red-color;
      border-radius: 5px;
      cursor: pointer;
      transition-duration: .5s;
      background-color: $app-background-color;

      &:hover {
        transform: scale(1.03);
        box-shadow: 0 0 5px 5px $red-color;
      }

      &:active {
        box-shadow: 0 0 15px 15px $red-color;
      }
    }
  }
}